
.title {
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    text-align: center;
}

.title h2 {
    margin: 0px auto;
    font-family: Montserrat, sans-serif;
    line-height: 1.2;
    letter-spacing: 5px;
    font-size: 36px;
    font-weight: 400;
    text-transform: capitalize;
}

.title__line {
    height: 1px;
    width: 55px;
    background-color: darkcyan ;
    margin: 30px 0;
}


